#contact {
  min-height: 70vh;
  background: #e5e5e9;
  color: rgb(8, 8, 32);
  padding: 2rem;
  h1 {
    margin: 5rem;
    font-size: 1.6rem;
    &:after {
      content: ".";
      animation: 2s linear infinite dotanim;
    }
  }
  form#contactForm {
    margin: 5rem auto;
    @include flex(column, center, center);
    width: 80%;
    gap: 1.2rem;
    input,
    textarea {
      border-radius: 13px;
      border: none;
      padding: 1rem;
      outline: none;
      width: 100%;
      @include transation(0.4s);
      &:focus {
        box-shadow: 0px 0px 10px 10px rgba(0, 0, 0, 0.342);
      }
    }

    .inputs {
      width: 100%;
      @include flex(row, space-evenly, center);
      margin-bottom: 1.2rem;
      gap: 0.5rem;
    }
    .submit {
      cursor: pointer;
      &:hover {
        transform: scale(1.2);
      }
    }
  }
}

@include medi {
  #contact {
    h1 {
      margin: 2rem;
      font-size: 1rem;
    }
    form {
      .inputs {
        flex-direction: column;
        gap: 1.2rem;
      }
    }
  }
}

@include mobile {
  #contact form {
    width: 100%;
  }
}

@keyframes dotanim {
  50% {
    content: "..";
  }
  100% {
    content: "...";
  }
}
